.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal {
  background: white;
  padding: 20px;
  border-radius: 2px;
  width: 400px;
  max-width: 90%;
  box-shadow: 0px 12px 17px 2px rgba(0, 0, 0, 0.14);
  position: relative;
}

.modal-close {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: rgba(159, 177, 189, 1);
}

.modal-content {
  margin-top: 20px;
}
.modal-overlay.pdf-modal .modal {
  padding: 0;
  width: 300px;
}
/* .modal-overlay.pdf-modal .modal-close {
  display: none;
} */
.modal-overlay.pdf-modal .pdf-modal-heading,
.modal-overlay.pdf-modal .pdf-modal-desc {
  padding: 5px 20px;
  margin: 0;
  margin-bottom: 5px;
}
.modal-overlay.pdf-modal .modal-content {
  margin-top: 10px;
}
.modal-overlay.pdf-modal .pdf-modal-desc {
  cursor: pointer;
  background: rgba(255, 255, 255, 1);
  box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.2);
}
.modal-overlay.pdf-modal .pdf-modal-heading {
  background: #fafafa;
}
.modal-overlay.pdf-modal .pdf-modal-desc:hover {
  background: #f3f2f2;
}
