.step-footer {
  justify-content: space-between;
  background-color: var(--Blue-90, #0f2731);
  display: flex;
  gap: 20px;
  font-size: 16px;
  line-height: 150%;
  padding: 12px 40px;
  position: sticky;
  bottom: 0;
  z-index: 2000;
}

@media (max-width: 991px) {
  .step-footer {
    flex-wrap: wrap;
    padding: 0 20px;
  }
}

.step-info {
  color: var(--Text-text-reversed, #fff);
  font-family: Avenir Next, sans-serif;
  font-weight: 600;
  margin: auto 0;
  min-width: 250px;
}

@media (max-width: 991px) {
  .step-info {
    max-width: 100%;
  }
}

.step-actions {
  justify-content: end;
  display: flex;
  gap: 8px;
  font-weight: 500;
  text-align: center;
}

.step-cancel {
  font-family: Avenir Next, sans-serif;
  justify-content: center;
  border-radius: 2px;
  background-color: var(--Button-button-secondary, #dce3e8);
  color: var(--Button-Text-button-secondary-text, #1c2b36);
  white-space: nowrap;
  padding: 12px 32px;
}

@media (max-width: 991px) {
  .step-cancel {
    white-space: initial;
    padding: 0 20px;
  }
}

.step-save {
  font-family: Avenir Next, sans-serif;
  justify-content: center;
  border-radius: 2px;
  background-color: var(--Button-button-primary, #186ade);
  color: var(--Button-Text-button-primary-text, #fff);
  padding: 12px 32px;
}

@media (max-width: 991px) {
  .step-save {
    padding: 0 20px;
  }
}

.step-save.disabled {
  opacity: 0.6;
  /* cursor: not-allowed; */
  background-color: #cccccc;
  color: #666666;
}
